/**
 * 
 * @param {(Object|string|number)} time
 * @param {string} cFormat
 * @returns {string}
 */
export function parseTime(time, cFormat) {
  if (arguments.length === 0) {
    return null;
  }
  const format = cFormat || "{y}-{m}-{d} {h}:{i}:{s}";
  let date;
  if (typeof time === "object") {
    date = time;
  } else {
    if (typeof time === "string" && /^[0-9]+$/.test(time)) {
      time = parseInt(time);
    }
    if (typeof time === "number" && time.toString().length === 10) {
      time = time * 1000;
    }
    date = new Date(time);
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay(),
  };
  const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
    let value = formatObj[key];
    // Note: getDay() returns 0 on Sunday
    if (key === "a") {
      return ["", "", "", "", "", "", ""][value];
    }
    if (result.length > 0 && value < 10) {
      value = "0" + value;
    }
    return value || 0;
  });
  return time_str;
}

/**
 * 
 * @param {(string|number)} faultDate
 * @param {(string|number)} completeTime
 * @returns {string}
 */
export const dateDifference = (faultDate, completeTime) => {
  let stime = new Date(faultDate).getTime();
  let etime = new Date(completeTime).getTime();
  let usedTime = etime - stime; //
  let days = Math.floor(usedTime / (24 * 3600 * 1000));
  //
  let leave1 = usedTime % (24 * 3600 * 1000); //
  let hours = Math.floor(leave1 / (3600 * 1000));
  //
  let leave2 = leave1 % (3600 * 1000); //
  let minutes = Math.floor(leave2 / (60 * 1000));
  // let time = days + ""+hours+""+minutes+"";
  let time = days;
  return time;
};
